    mutation MyMutation {
        update_retro_advertisements_item(
        data: {heading: "${headingText.value}", description: "${descriptionText.value}"}
        id: "${profileAdvertisementId}"){
            id,
            description,
        }
    }
`);

    if (!headingText.value || !descriptionText.value) {
